import React from "react";
import ReactDOM from "react-dom";
import './WorkExpSec.scss';
import Media from 'react-bootstrap/Media'

// function ToDoItem(props){
//     return(
//         <div className ="todo-item">
//             <input type="checkbox" checked={props.todo.completed}/>
//             <p>{props.todo.item}</p>
//         </div>
//     )
// }

class WorkExpSec extends React.Component{
    
    render(){
        const description = [];
        for(const value in this.props.workexp.Description.entries()){
            description.push(<li>{value}</li>)
        }

        this.props.workexp.Description.map(item => 
            description.push(<li>{item}</li>)
        )


        let Sec;
        if (this.props.workexp.id % 2 == 1){
            Sec =   <Media>
                        <img width={this.props.workexp.width} height={100} className="align-self-center mr-3" src={require(`../../img/${this.props.workexp.logo}`)} alt={this.props.workexp.CompanyName}/>
                         <Media.Body>
                             <h4>{this.props.workexp.CompanyName}</h4>
                             <div className = "pos-line">
                                <p>{this.props.workexp.JobTitle}</p>
                                <p>{this.props.workexp.TimeWork}</p>
                             </div>

                            <ul>
                                {description}
                            </ul>

                        </Media.Body>
                    </Media>
        }else{
            Sec = <Media>
                    
                    <Media.Body>
                        <h4>{this.props.workexp.CompanyName}</h4>
                        <div className = "pos-line">
                            <p>{this.props.workexp.JobTitle}</p>
                            <p>{this.props.workexp.TimeWork}</p>
                        </div>

                        <ul>
                            {description}
                        </ul>
                    </Media.Body>
                    <img width={this.props.workexp.width} height={100} className="align-self-center mr-3" src={require(`../../img/${this.props.workexp.logo}`)} alt={this.props.workexp.CompanyName}/>
                 </Media>
        
        }


        return(
            <div className="compDiv">

                {Sec}


            </div>
        )
    }
  }
export default WorkExpSec;